import { INSERT_PRIVATE_SHARE_DECK_LIST } from '../actions/dashboardActions'

const privateSharedDeckReducer = (state = [], action) => {
    switch (action.type)
    {
        case INSERT_PRIVATE_SHARE_DECK_LIST:
            return [...state];
        default:
            return state;
    }
};

export default privateSharedDeckReducer;