import { combineReducers } from 'redux';
import publicSharedDeckReducer from './publicSharedDeckReducer';
import privateSharedDeckReducer from './privateSharedDeckReducer';
import recommendDeckReducer from './recommendDeckReducer';
import dmProductReducer from './dmProductReducer';
import uiEventReducer from './uiEventReducer';
import { dmCardReducer } from './dmCardReducer';
import { dmDeckReducer } from './dmDeckReducer';

const appReducer = combineReducers({
    publicSharedDecks: publicSharedDeckReducer,
    privateSharedDecks: privateSharedDeckReducer,
    recommendDecks: recommendDeckReducer,
    dmProductList: dmProductReducer,
    dmCardList: dmCardReducer,
    dmDeckList: dmDeckReducer,
    uiEvents: uiEventReducer
});

export default appReducer; 