import React, { Component, Fragment } from 'react';
import classnames from 'classnames';
import {FormattedMessage} from 'react-intl';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from "react-router";
import { Grid, IconButton, AppBar, Toolbar, LinearProgress, Fab } from '@material-ui/core';
import { Sort, FilterList, ArrowUpward, Archive } from '@material-ui/icons';
import { connect } from 'react-redux';
import CardView from '../dm_components/CardView';
import GridView from '../ui_components/GridView';
import { getCardsBySetId, getCardsByDeckId } from '../redux/actions/dmCardActions';
import { insertCardsToDeck } from '../redux/actions/dmDeckActions';
import CardDetailDialog from '../dm_components/CardDetailDialog';
import InsertCardDialog from '../dm_components/InsertCardDialog';


const styles = theme => ({
    flexColumnBox: {
      display: 'flex',
      flexFlow: 'column',
      height: '100%'
    },
    gridWrapper: {
      height: '100%'
    },
    firstColumn: {
      display: 'flex',
      flexFlow: 'column'
    },
    secondColumn: {
      display: 'grid'
    },
    grow: {
      flexGrow: 1,
    },
    gridTitle: {
      fontWeight: 'bold'
    },
    topFab: {
      position: 'fixed',
      right: theme.spacing.unit * 2,
      bottom: theme.spacing.unit * 2
    },
    [theme.breakpoints.up('lg')]: {
      firstColumn: {
        gridTemplateRows: 'auto'
      },
      secondColumn: {
        gridTemplateRows: 'auto'
      }
    }
});

const mapStateToProps = state => {
  return {
    dmCardList: state.dmCardList,
    apiCallStatus: state.uiEvents.apiCallStatus
  };
}

const mapDispatchToProps = dispatch => {
  return {
    getCardsBySetId: (setId) => {dispatch(getCardsBySetId(setId))},
    getCardsByDeckId: (deckId) => {dispatch(getCardsByDeckId(deckId))}
  };
}

class CardGallery extends Component 
{
  constructor(props)
  {
      super(props);
      this.state = {
        card: null,
        showCardDetailDialog : false,
        showInsertCardDialog : false
      };
  }

  componentDidMount()
  {
    const { location } = this.props;
    switch(location.state.action)
    {
      case "getCardsBySetId":
        this.props.getCardsBySetId(location.state.primaryId);
        break;
      case "getPlayerDeck":
        this.props.getCardsByDeckId(location.state.primaryId);
        break;
    }
  }

  handleDialogOpen = (card) => {
    this.setState({ showCardDetailDialog: true, card: card });
  }

  handleCardDetailDialogClose = () => {
    this.setState({ showCardDetailDialog: false, card: null });
  }

  handleInsertCardDialogClose = () => {
    this.setState({ showInsertCardDialog: false, card: null });
  }

  render() 
  {
    const { classes, location, apiCallStatus, dmCardList } = this.props;
    const { showCardDetailDialog, showInsertCardDialog, card } = this.state;
    let fakeList = [
        {
            "_id": "5bccf26c1c9d440000668ad7",
            "setId": "dm01",
            "setNumber": "043",
            "name": "大気の使徒フレイ",
            "rarityCd": "U"
        }
    ];

    return (
      <Fragment>
        <AppBar position="static" color="default">
          <Toolbar>
            <Typography variant="h6" color="textSecondary" className={classnames(classes.grow, classes.gridTitle)}>{location.state.title}</Typography>
            <IconButton>
                <FilterList color="secondary"/>
            </IconButton>
            <IconButton>
                <Sort color="secondary"/>
            </IconButton>
          </Toolbar>
        </AppBar>
        { apiCallStatus==="started" && <LinearProgress /> }
        <GridView>
          { dmCardList.map((card) => <CardView key={card._id} card={card} handleClick={this.handleDialogOpen}/>) } 
        </GridView>
        <Fab color="primary" className={classes.topFab} onClick={() => window.scrollTo(0,0)}>
          <ArrowUpward />
        </Fab>
        <CardDetailDialog open={showCardDetailDialog} card={card} handleClose={this.handleCardDetailDialogClose}
          deckAction= {
            <IconButton onClick={() => this.setState({ showInsertCardDialog: true, showCardDetailDialog: false })}>
                <Archive color="secondary" />
            </IconButton>
          }/>
          <InsertCardDialog open={showInsertCardDialog} card={card} handleClose={this.handleInsertCardDialogClose}/>
      </Fragment>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(withStyles(styles)(CardGallery)));