import React from 'react';
import Typography from '@material-ui/core/Typography';
import { Dialog, DialogContent, IconButton, Divider, CircularProgress } from '@material-ui/core';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import { Close } from '@material-ui/icons';
import { withStyles } from '@material-ui/core/styles';
import App_Config from '../config/app-config.json';

const styles = theme => ({
    dialogTitle: {
        display: 'flex',
        alignItems: 'center'
    },
    cardImage: {
        width: '215px'
    },
    grow: {
        flexGrow: 1,
      },
    gridContainer: {
        display: 'grid',
        gridGap: '10px',
        marginTop: '10px'
    },
    gridItemImgContainer: {
        textAlign: 'center'
    },
    gridItemDetailContainer: {
        fontFamily: 'Meiryo',
    },
    effectList: {
        listStyleType: 'square'
    },
    [theme.breakpoints.up('sm')]: {
        gridContainer: {
        gridTemplateColumns: 'auto auto'
      }
    }
});

function CardDetailDialog({ classes, open, card, handleClose, deckAction }) {
    // console.log(card);
    if(card === null) return null;
    
    return (
        <Dialog open={open} onClose={handleClose}>
            <MuiDialogTitle disableTypography className={classes.dialogTitle}>
                <Typography variant="h6">Card Detail</Typography>
                <div className={classes.grow} />
                {deckAction}
                <IconButton onClick={() => handleClose()}>
                    <Close color="secondary" />
                </IconButton>
            </MuiDialogTitle>
            <DialogContent>
                <div className={classes.gridContainer}>
                    <div className={classes.gridItemImgContainer}>
                        <img src={App_Config.dmCardImageLink + card.setId + "-" + card.setNumber + ".jpg"} className={classes.cardImage} />
                    </div>
                    <div className={classes.gridItemDetailContainer}>
                        {card.name} {card.setId.toUpperCase()} {card.setNumber}<br />
                        {card.type} {card.civilizations.map((civil) => civil)} レアリティ {card.rarityCd}<br />
                        コスト {card.cost} マナ {card.mana} パワー {card.power!==undefined && card.power}<br />
                        {card.races !== undefined && (card.races.map((race) => race))}
                        <Divider light />
                        <ul className={classes.effectList}>
                            {card.effects.map((effect, index) => <li key={card.setNumber+index}>{effect}</li>)}
                        </ul>
                    </div>
                </div>
            </DialogContent>
        </Dialog>
    );
}

export default withStyles(styles)(CardDetailDialog);