import React from 'react';
import './SideNav.css';
import { Tooltip, IconButton } from '@material-ui/core';
import { Collections, Work, Language ,PowerSettingsNew } from '@material-ui/icons';
import { Link } from "react-router-dom";
import dmIcon from '../images/dm__icon.png'

function SideNav(props) {
    return (
        <div id="sidenav">
            <div id="topSideNav">
                <img alt="DM" src={dmIcon} id="dmIcon"/>
                <hr/>
                <Link to="/deckManager">
                    <Tooltip title="Deck Manager" placement="right">
                        <IconButton>
                            <Work />
                        </IconButton>
                    </Tooltip>
                </Link>
                <Link to="/productGallery">
                    <Tooltip title="Product Gallery" placement="right">
                        <IconButton>
                            <Collections />
                        </IconButton>
                    </Tooltip>
                </Link>
            </div>
            <div id="bottomSideNav">
                <Tooltip title="Langauges Setting" placement="right">
                    <IconButton onClick={(e) => props.showLanguageMenu(e)}>
                        <Language />
                    </IconButton>
                </Tooltip>
                <Tooltip title="Logout" placement="right">
                    <IconButton onClick={() => props.showLogoutDialog()}>
                        <PowerSettingsNew />
                    </IconButton>
                </Tooltip>
            </div>
        </div>
    );
}
export default SideNav;