import React, { Component, Fragment } from 'react';
import classnames from 'classnames';
import {FormattedMessage} from 'react-intl';
import Typography from '@material-ui/core/Typography';
import { withStyles } from '@material-ui/core/styles';
import { withRouter } from "react-router";
import { Grid, IconButton, AppBar, Toolbar, LinearProgress, Fab } from '@material-ui/core';
import { Edit, Sort, FilterList, Delete, Save, DeleteSweep } from '@material-ui/icons';
import { connect } from 'react-redux';
import CardView from '../dm_components/CardView';
import GridView from '../ui_components/GridView';
import { getPlayerDeck, updatePlayerDecks, deletePlayerDeck } from '../redux/actions/dmDeckActions';
import CardDetailDialog from '../dm_components/CardDetailDialog';
import DeckNameDialog from '../dm_components/DeckNameDialog';
import ConfirmDialog from '../dm_components/ConfirmDialog'


const styles = theme => ({
    flexColumnBox: {
      display: 'flex',
      flexFlow: 'column',
      height: '100%'
    },
    gridWrapper: {
      height: '100%'
    },
    firstColumn: {
      display: 'flex',
      flexFlow: 'column'
    },
    secondColumn: {
      display: 'grid'
    },
    grow: {
      flexGrow: 1,
    },
    gridTitle: {
      fontWeight: 'bold'
    },
    saveFab: {
      position: 'fixed',
      right: theme.spacing.unit * 2,
      bottom: theme.spacing.unit * 2
    },
    deleteFab: {
        position: 'fixed',
        right: theme.spacing.unit * 4 + 56,
        bottom: theme.spacing.unit * 2
    },
    [theme.breakpoints.up('lg')]: {
      firstColumn: {
        gridTemplateRows: 'auto'
      },
      secondColumn: {
        gridTemplateRows: 'auto'
      }
    }
});

const mapStateToProps = state => {
  return {
    dmDeck: state.dmDeckList[0],
    apiCallStatus: state.uiEvents.apiCallStatus
  };
}

const mapDispatchToProps = dispatch => {
  return {
    getPlayerDeck: (deckId) => {dispatch(getPlayerDeck(deckId))},
    updatePlayerDecks: (deckObject) => {dispatch(updatePlayerDecks(deckObject._id, deckObject))},
    deletePlayerDeck: (deckId) => {dispatch(deletePlayerDeck(deckId))}
  };
}

class DeckEditor extends Component 
{
  constructor(props)
  {
      super(props);
      this.state = {
        card: null,
        cachedDeck: null,
        showCardDetailDialog : false,
        showDeckNameDialog: false,
        showConfirmDialog: false
      };
  }

  componentDidMount()
  {
    const { location, getPlayerDeck } = this.props;
    getPlayerDeck(location.state.deckId);
  }

  componentDidUpdate()
  {
    const { cachedDeck } = this.state;
    const { dmDeck } = this.props;
    console.log(dmDeck);
    if(cachedDeck === null && dmDeck !== undefined)
    {
      this.setState({ cachedDeck: dmDeck });
    }
  }

  editDeckName = (deckName) => {
    const { cachedDeck } = this.state;
    let newDeck = { ...cachedDeck };
    newDeck.name = deckName;
    this.setState({ cachedDeck: newDeck });
  }

  removeCardFromDeck = (removingCard) => {
    const { cachedDeck } = this.state;
    let newDeck = { ...cachedDeck };
    newDeck.cards = cachedDeck.cards.filter(card => card !== removingCard);
    this.setState({ cachedDeck: newDeck });
    this.handleDialogClose();
  }

  handleDialogOpen = (card) => {
    this.setState({ showCardDetailDialog: true, card: card });
  }

  handleDialogClose = () => {
    this.setState({ showCardDetailDialog: false, card: null });
  }

  closeCreateDeckDialog = () => {
    this.setState({showDeckNameDialog: false})
  }

  render() 
  {
    const { classes, apiCallStatus, dmDeck, updatePlayerDecks, deletePlayerDeck } = this.props;
    const { card, cachedDeck, showDeckNameDialog, showCardDetailDialog, showConfirmDialog } = this.state;

    if(cachedDeck===null) return null;

    return (
      <Fragment>
        <AppBar position="static" color="default">
          <Toolbar>
            <Typography variant="h6" color="textSecondary" className={classes.gridTitle}>{cachedDeck.name}</Typography>
            <IconButton onClick={() => this.setState({showDeckNameDialog: true})}>
                <Edit color="secondary"/>
            </IconButton>
            <div className={classes.grow} />
            <IconButton>
                <FilterList color="secondary"/>
            </IconButton>
            <IconButton>
                <Sort color="secondary"/>
            </IconButton>
          </Toolbar>
        </AppBar>
        { apiCallStatus==="started" && <LinearProgress /> }
        <GridView>
          { cachedDeck.cards.map((card, index) => <CardView key={card._id+index} card={card} handleClick={this.handleDialogOpen}/>)  } 
        </GridView>
        <Fab color="primary" className={classes.deleteFab} onClick={() => this.setState({showConfirmDialog: true})}>
          <Delete />
        </Fab>
        <Fab color="primary" className={classes.saveFab} onClick={() => {updatePlayerDecks(cachedDeck);this.props.history.push('/deckManager');}}>
          <Save />
        </Fab>
        <CardDetailDialog open={showCardDetailDialog} card={card} handleClose={this.handleDialogClose}
          deckAction= {
            <IconButton onClick={() => this.removeCardFromDeck(card)}>
                <DeleteSweep color="secondary" />
            </IconButton>
          }/>
        <DeckNameDialog open={showDeckNameDialog} title="Edit Deck Name" handleDeckName={this.editDeckName} closeDialog={this.closeCreateDeckDialog}/>
        <ConfirmDialog open={showConfirmDialog}
          dialogTitle="Delete Deck"
          dialogMessage="Are you sure you want to delete this deck?"
          closeDialog={() => this.setState({showConfirmDialog: false})}
          submitButtonText="Delete"
          submitAction={() => {deletePlayerDeck(cachedDeck._id);this.props.history.push('/deckManager');}}/>
      </Fragment>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(withStyles(styles)(DeckEditor)));